import React, { Component } from 'react';
import { connect } from "react-redux";
import { Button } from 'react-bootstrap';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../Assets/AssetsHelper';
import { MEDIA_REPLACE_URL } from '../../../Services/Utils/utils'
import ProfileImage from '../CommonComponents/ProfileImage/ProfileImage';

class ViewProfile extends Component {

    editProile = event => {
        this.props.editProileCallback(event);
    }
    render() {
        const { profile, userData, themeSkin } = this.props
        return userData && Object.keys(userData).length > 0 ? <div className="w-100">
                    <div className="d-flex align-items-center flex-wrap flex-grow-1 pb-5 mb-10 border-bottom">
                        <div className="me-3 me-xxl-4">
                            <ProfileImage className={'symbol h-90px w-90px position-relative rounded-circle overflow-hidden bg-light'}
                                imageSize={90}
                                symbolClass={'symbol-label text-light-alt fw-bold display-6 text-uppercase'}
                                name={userData.user.name} profileUrl={MEDIA_REPLACE_URL(userData.user.picture)} networkId={0} />
                        </div>
                        <div className="d-flex flex-column my-2">
                            <div className="d-flex align-items-center mb-2">
                                <h6 className="fs-4 text-dark text-hover-primary mb-0 me-1">{userData.user.name}</h6>
                                {userData.user.access == 1 ? <span className="svg-icon svg-icon-1 svg-icon-primary">
                                    <SVG src={baseUrl('media/icons/svg-icons/check-pattern.svg')} height={20} width={20} />
                                </span> : null}
                            </div>
                            <div className="d-flex flex-wrap pe-2">
                                <p className="d-flex align-items-center text-decoration-none text-hover-primary mb-2 fw-medium">
                                    <i className="ri-mail-line icon-6 text-dark me-1"></i>
                                    {userData.user.emailId}
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex align-items-center justify-content-between flex-wrap py-1 mb-3">
                        <div className="">
                            <h3 className="fs-6 text-gray-800">Personal Info</h3>
                        </div>
                        <div className="d-flex">
                            <Button varient={'primary'} onClick={()=>this.editProile(true)} size={'sm'}>Edit Profile</Button>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Full Name</label>
                        <div className="col-sm-8">
                            <span className="fw-bold fs-7 text-gray-800">{userData.user.name}</span>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Email</label>
                        <div className="col-sm-8">
                            <span className="fw-bold fs-7 text-gray-800 me-2">{userData.user.emailId}</span>
                            <span className={`badge text-light-alt ${userData.user.access == 1 ? `bg-success` : `bg-warning`}`}>{userData.user.access == 1 ? `Verified` : `Pending`}</span>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Contact Phone</label>
                        <div className="col-sm-8 d-flex align-items-center">
                            <span className="fw-bold fs-7 text-gray-800 me-2">{userData.user.contact_no ? userData.user.contact_no : '--'}</span>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Industry</label>
                        <div className="col-sm-8 fv-row">
                            <span className="fw-semibold text-gray-800 fs-7">{userData.user.industry ? userData.user.industry : '--'}</span>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Organization</label>
                        <div className="col-sm-8">
                            <span className="fw-semibold fs-7 text-gray-800 text-hover-primary">{userData.user.organization ? userData.user.organization : '--'}</span>
                        </div>
                    </div>
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-4 text-gray-500">Department</label>
                        <div className="col-sm-8">
                            <span className="fw-semibold fs-7 text-gray-800 text-hover-primary">{userData.user.department ? userData.user.department : '--'}</span>
                        </div>
                    </div>
                    {userData.user.access == 2 ? <div className="d-flex alert alert-warning">
                        <i className="ri-information-line icon-2 text-warning me-2"></i>
                        <div className="d-flex flex-column flex-stack flex-grow-1">
                            <h4 className="fs-6 fw-medium mb-1">Your email needs to be verified!</h4>
                            <p className="mb-0">We have sent you and email with the verfication link to <strong>{userData.user.emailId}</strong>. Kindly click on the verfication link present in the email to start exploring UGC Suit further.</p>
                        </div>
                    </div> : null}
                </div>: null

    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData,
        themeSkin: state.themeSkin
    }
}
export default connect(mapStateToProps)(ViewProfile);